@import "sass/_variables.scss";@import "sass/_themes.scss"; 
.cq-chart-settings {
    display: flex;
    flex-direction: column;
    height: auto !important;

    .ciq-num-input > input[type=number] {
        width: 5.5em;
    }
    .buttons {
        display: flex;
        justify-content: space-between;
        padding: 16px 24px 24px;
        border-radius: 0 0 $border-radius $border-radius;
        height: 72px;
        box-sizing: border-box;
        @include themify($themes) {
            background: themed('DialogFootBg');
            border-top: 1px solid themed('DialogFootBorder');
        }

        .sc-btn--delete {
            cursor: pointer;
            padding: 5px;
            width: 32px;
            height: 32px;
            border-radius: $border-radius;

            svg {
                @include themify($themes) {
                    fill: themed('DialogFootDeleteIcon');
                }
            }
            &:hover {
                @include themify($themes) {
                    background: themed('DialogFootDeleteHoverBg');
                }
            }
        }
        .sc-btn--reset {
            margin-right: 8px;
            padding: 5px 12px;
        }
    }
    input[type=number] {
        display: inline-block;
        width: 3.2em;
        line-height: 1.6em;
        margin-right: 5px;
        padding-left: 6px;
        border-radius: 3px;
        font-size: 14px;
    }
}
